/* 
  全局样式：
*/

html,
body {
  color: #333;
  background-color: #fff;
  font-family: 'Microsoft YaHei';
}

* {
  box-sizing: border-box;

  /* 参考链接：https://www.jianshu.com/p/04bf173826aa */
  /* 解决移动端滑动事件优化问题的，在当前项目中，解决 轮播图组件 手动滑动时，报错的问题 */
  touch-action: pan-y;
}

html,
body,
#root,
.app {
  height: 100%;
}
* {
  margin: 0;
  padding: 0;
}
p,
h1,
h2,
h3 {
  margin: 0;
}

a {
  color: #333;
}
